___CSS_LOADER_EXPORT___.push([module.id, `.button {
  display: inline-block;
  padding: var(--padding-s) var(--padding-l);
  font-size: var(--font-size-normal);
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.1s ease;
  background-color: var(--color-primary);
  color: var(--color-text-on-primary);
}

.button:hover, .button:focus {
  background-color: var(--color-primary-dark);
  color: var(--color-text-on-primary); /* Pastikan kontras tetap baik */
  transform: translateY(-1px);
  text-decoration: none;
  outline: 2px solid var(--color-primary-dark); /* Focus indicator */
  outline-offset: 2px;
}

.button:active {
  transform: translateY(0px);
}

.button.button-primary {
  background-color: var(--color-primary-dark);
  color: var(--color-text-on-primary);
}
.button.button-primary:hover, .button.button-primary:focus {
  background-color: var(--color-primary); /* Lebih terang saat hover */
}

.button.button-secondary {
  background-color: var(--color-secondary);
  color: var(--color-text-on-secondary);
}
.button.button-secondary:hover, .button.button-secondary:focus {
  background-color: var(--color-secondary-dark);
}

.button.button-danger {
  background-color: var(--color-error);
  color: #fff;
}
.button.button-danger:hover, .button.button-danger:focus {
  background-color: #d32f2f; /* Warna merah lebih gelap */
}

.button.button-outline {
  background-color: transparent;
  color: var(--color-primary-dark);
  border: 2px solid var(--color-primary-dark);
}
.button.button-outline:hover, .button.button-outline:focus {
  background-color: var(--color-primary-light);
  color: var(--color-primary-dark);
}

/* Floating Action Button (FAB) style */
.button-fab {
  position: fixed;
  bottom: var(--padding-l);
  right: var(--padding-l);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-color: var(--color-secondary);
  color: var(--color-text-on-secondary);
  font-size: 1.5rem; /* Untuk ikon '+' */
  line-height: 56px; /* Vertically center text/icons */
  text-align: center;
  box-shadow: 0 4px 8px var(--color-shadow);
  padding: 0; /* Reset padding default */
}

.button-fab:hover, .button-fab:focus {
  background-color: var(--color-secondary-dark);
}

/* Anda mungkin sudah punya rule .button dasar, kita akan tambahkan styling untuk yang spesifik */

/* Styling Umum untuk Tombol Aksi */
.story-item__actions .button {
  display: inline-flex; /* Agar ikon dan teks bisa diatur posisinya */
  align-items: center;  /* Posisikan ikon dan teks di tengah secara vertikal */
  justify-content: center; /* Posisikan di tengah secara horizontal */
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.1s ease;
  color: white;
}

.story-item__actions .button:hover {
  opacity: 0.9;
}

.story-item__actions .button:active {
  transform: scale(0.98); /* Efek saat tombol ditekan */
}

/* Ikon di dalam tombol */
.story-item__actions .button i {
  margin-right: 8px; /* Beri jarak antara ikon dan teks */
}

/* Tombol Simpan */
.button-save-offline {
  background-color: #3498db; /* Warna biru yang bagus */
}

.button-save-offline:hover {
  background-color: #2980b9; /* Warna biru lebih gelap saat hover */
}

/* Tombol Hapus */
.button-delete-offline {
  background-color: #e74c3c; /* Warna merah untuk aksi destruktif */
}

.button-delete-offline:hover {
  background-color: #c0392b; /* Warna merah lebih gelap saat hover */
}

/* Tambahan: Styling untuk kontainer tombol agar rapi */
.story-item__actions {
  display: flex;
  justify-content: flex-end; /* Posisikan tombol ke kanan */
  gap: 10px; /* Beri jarak antar tombol jika ada lebih dari satu */
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #eee; /* Beri garis pemisah (opsional) */
}
