___CSS_LOADER_EXPORT___.push([module.id, `.detail-story-container {
  max-width: 800px; /* Batasi lebar untuk keterbacaan */
  margin: var(--margin-l) auto;
  padding: var(--padding-l);
  background-color: var(--color-surface);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.story-detail__name {
  font-size: var(--font-size-xlarge);
  color: var(--color-primary-dark);
  margin-bottom: var(--margin-xs);
  text-align: center;
}

.story-detail__date {
  font-size: var(--font-size-small);
  color: var(--color-text-light);
  text-align: center;
  margin-bottom: var(--padding-l);
}

.story-detail__figure {
  margin: 0 0 var(--padding-l) 0;
  text-align: center; /* Pusatkan gambar jika lebih kecil dari container */
}

.story-detail__image {
  width: 100%; /* Gambar mengisi lebar container detail */
  max-width: 600px; /* Batasi lebar maksimum gambar */
  height: auto;
  border-radius: var(--border-radius);
  object-fit: cover;
  margin: 0 auto var(--margin-s) auto; /* Pusatkan gambar jika max-width tercapai */
  border: 1px solid var(--color-border);
}

.story-detail__description h3,
.story-detail__location h3 {
  font-size: var(--font-size-medium);
  color: var(--color-primary-dark);
  margin-top: var(--padding-l);
  margin-bottom: var(--margin-s);
  border-bottom: 1px solid var(--color-border);
  padding-bottom: var(--padding-xs);
}

.story-detail__description p {
  line-height: 1.7;
  color: var(--color-text);
}

.story-detail__map-display {
  border-radius: var(--border-radius);
  border: 1px solid var(--color-border);
}

/* Untuk menyembunyikan figcaption secara visual tapi tetap aksesibel */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
