___CSS_LOADER_EXPORT___.push([module.id, `.form-group {
  margin-bottom: var(--margin-m);
}

.form-group label {
  display: block;
  margin-bottom: var(--margin-xs);
  font-weight: bold;
  color: var(--color-text-light);
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="password"],
.form-group input[type="number"],
.form-group input[type="date"],
.form-group textarea,
.form-group select {
  width: 100%;
  padding: var(--padding-s);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  font-size: var(--font-size-normal);
  font-family: var(--font-primary);
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--color-primary-dark);
  box-shadow: 0 0 0 2px rgba(var(--color-primary-rgb, 160, 210, 219), 0.5); /* Buat --color-primary-rgb jika perlu */
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

/* Styling untuk input file yang lebih baik (jika diperlukan) */
.file-input {
  display: block; /* Atau inline-block tergantung kebutuhan */
  margin-top: var(--margin-xs);
}

.preview-image, .camera-feed {
  margin-top: var(--margin-s);
  max-width: 300px; /* Batasi ukuran preview */
  max-height: 200px;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
}

/* ... (gaya form yang sudah ada) ... */

.auth-container {
  max-width: 400px;
  margin: var(--margin-xl) auto;
  padding: var(--padding-l);
  background-color: var(--color-surface);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.auth-container h2 {
  text-align: center;
  margin-bottom: var(--padding-l);
  color: var(--color-primary-dark);
}

.auth-container p {
  text-align: center;
  margin-top: var(--padding-m);
  font-size: var(--font-size-small);
}

.auth-container .message {
  text-align: center;
  margin-top: var(--padding-m);
  font-weight: bold;
}

.add-story-container .photo-source-options {
  display: flex;
  align-items: center;
  gap: var(--margin-s);
  margin-bottom: var(--margin-s);
}

.file-input-hidden {
  display: none; /* Sembunyikan input file asli */
}

/* Tombol untuk trigger input file bisa di-style seperti button biasa */
.add-story-container label.button { /* Target label yang berperan sebagai button */
  cursor: pointer;
}

.camera-feed {
  width: 100%;
  max-width: 320px; /* Sesuaikan ukuran */
  height: auto;
  border: 1px solid var(--color-border);
  margin-top: var(--margin-s);
  margin-bottom: var(--margin-s);
  background-color: #333; /* Latar belakang gelap untuk feed kamera */
}

#capturePhotoButton, #stopCameraButton {
  margin-top: var(--margin-xs);
  margin-right: var(--margin-xs);
}

#add-story-loader {
  margin-top: var(--margin-m);
}
